import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Button, Form, Modal, Select } from 'antd';
import { connect } from 'react-redux';
import * as actions from '../../store/actions';
import classes from './../Projects/Projects.module.css';
import { backendUrl } from '../../store/utility';
import editProjectModal from './Modals/EditProjectModal';
import EditProjectModal from './Modals/EditProjectModal';

const ProjectInfo = props => {
  const [id, setId] = useState('');
  const [project, setProject] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);

  const [projectName, setProjectName] = useState('');
  const [managerName, setManagerName] = useState('');
  const [projectStatus, setProjectStatus] = useState('');
  const [projectSize, setProjectSize] = useState('');
  const [quickWin, setQuickWin] = useState('');
  const [projectType, setProjectType] = useState('');

  console.log('this is the propssssssss' + props);

  const FormItem = Form.Item;
  const Option = Select.Option;

  // const editProjectModalVisible = useState(false);
  // const projectToEdit = useState(null);
  //
  // const showEditProjectModal = project =>
  //   this.setState({ editProjectModalVisible: true, projectToEdit: project });
  //
  // const hideEditProjectModal = () =>
  //   this.setState({ editProjectModalVisible: false, projectToEdit: null });
  //
  // const editAProject = values => {
  //   this.props.dispatch(editAProject(values));
  // };

  useEffect(() => {
    let infoId = props.match.params.id;
    setId(infoId);
    const url = `${backendUrl()}/projects/projectDetails/` + infoId;
    const method = 'GET';
    const header = { 'Content-Type': 'application/json' };

    fetch(url, {
      method: method,
      headers: header
    })
      .then(res => {
        if (res.status === 422) {
          throw new Error('Error');
        } else if (res.status !== 200 && res.status !== 201) {
          throw new Error('Error');
        }
        return res.json();
      })
      .then(resData => {
        console.log('res data project info', resData);
        setProject(resData.data);
        setIsLoaded(true);
      })
      .catch(error => {
        console.log('error occur', error);
      });
  }, []);

  const deleteProject = routeType => {
    let infoId = props.match.params.id;
    setId(infoId);
    const url = `http://localhost:8080/projects/${routeType}/ ` + infoId;
    const method = 'GET';
    const header = { 'Content-Type': 'application/json' };

    fetch(url, {
      method: method,
      headers: header
    })
      .then(res => {
        if (res.status === 422) {
          throw new Error('Error');
        } else if (res.status !== 200 && res.status !== 201) {
          throw new Error('Error');
        }
        console.log('check1');
        return res.json();
      })
      .then(window.location.reload())
      .catch(error => {
        console.log('error occur', error);
      });
  };

  const editProject = routeType => {
    console.log('goes to edit project');
    let infoId = props.match.params.id;
    setId(infoId);
    const url = `http://localhost:8080/projects/${routeType}/` + infoId;
    const method = 'PUT';
    const header = { 'Content-Type': 'application/json' };

    fetch(url, {
      method: method,
      header: header,
      body: {
        projectName: projectName,
        managerName: managerName,
        projectStatus: projectStatus,
        projectSize: projectSize,
        quickWin: quickWin,
        projectType: projectType
      }
    })
      .then(res => {
        if (res.status === 422) {
          throw new Error('Error');
        } else if (res.status !== 200 && res.status !== 201) {
          throw new Error('Couldnt edit project');
          console.log('there was an error editing project');
        }
        console.log('check edit is working');

        res.json().then(json => {
          console.log('this is the jsooooooooon' + json);
        });
        return res.json();
      })
      .catch(error => {
        console.log('error occurred', error);
      });
  };

  console.log(props.role);

  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isModalVisible2, setIsModalVisible2] = useState(false);
  const [isModalVisible3, setIsModalVisible3] = useState(false);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const handleOk = () => {
    setIsModalVisible(false);
    deleteProject('deleteProject');
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  const showModal2 = () => {
    setIsModalVisible2(true);
  };

  const handleOk2 = () => {
    setIsModalVisible2(false);
    deleteProject('archiveProject');
  };

  const handleCancel2 = () => {
    setIsModalVisible2(false);
  };

  const showModal3 = () => {
    setIsModalVisible3(true);
  };

  const handleOk3 = () => {
    setIsModalVisible3(false);
    editProject('editProject');
  };

  const handleCancel3 = () => {
    setIsModalVisible3(false);
  };

  return (
    <div className={classes.Projects}>
      <h1>Project Details</h1>
      <table className={classes.Table}>
        <thead>
          <tr>
            <th>Project Name</th>
            <th>Manager Name</th>
            <th>Proect Status</th>
            <th>Project Size</th>
            <th>Quick Win</th>
            <th>Project Type</th>
            <th>Project Created At</th>
            <th>Score Board</th>
            <th>Business Case</th>
          </tr>
        </thead>
        <tbody>
          {isLoaded ? (
            <tr key={project.id}>
              <td> {project.name}</td>
              <td>{project.managerName}</td>
              <td>{project.projectStatus}</td>
              <td>{project.projectSize}</td>
              <td>{project.quickWin}</td>
              <td>{project.projectType}</td>
              <td>{project.createdAt}</td>
              <td>
                <Link to={`/scoreboard/${project.id}`}>ScoreBoard</Link>{' '}
              </td>
              <td>
                <Link to={`/businessCase/${project.id}`}>Business Case</Link>{' '}
              </td>
            </tr>
          ) : (
            <tr>
              <td colSpan="8">Loading</td>
            </tr>
          )}
        </tbody>
      </table>

      <div className={classes.ProjectInfo} style={{ float: 'right' }}>
        {props.role === 'transformationTeam' && (
          <>
            <Modal
              title="Edit project "
              visible={isModalVisible3}
              onOk={handleOk3}
              onCancel={handleCancel3}
            >
              <form>
                <input
                  type="text"
                  placeholder="projectName"
                  value={projectName}
                  onChange={e => {
                    setProjectName(e.target.value);
                  }}
                />
                <input
                  type="text"
                  placeholder="managerName"
                  value={managerName}
                  onChange={e => {
                    setManagerName(e.target.value);
                  }}
                />
                <input
                  type="text"
                  placeholder="projectStatus"
                  value={projectStatus}
                  onChange={e => {
                    setProjectStatus(e.target.value);
                  }}
                />
                <input
                  type="text"
                  placeholder="projectSize"
                  value={projectSize}
                  onChange={e => {
                    setProjectSize(e.target.value);
                  }}
                />
                <input
                  type="text"
                  placeholder="quick win"
                  value={quickWin}
                  onChange={e => {
                    setQuickWin(e.target.value);
                  }}
                />
                <input
                  type="text"
                  placeholder="projectType"
                  value={projectType}
                  onChange={e => {
                    setProjectType(e.target.value);
                  }}
                />
              </form>

              {/*<Form>*/}
              {/*  <FormItem>*/}
              {/*    label="Project Name" name="projectName" initialValue=*/}
              {/*    {props.projectName}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        whitespace: true,*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter the new project Name'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*  </FormItem>*/}
              {/*  <FormItem>*/}
              {/*    label="Manager Name" name="managerName" initialValue=*/}
              {/*    {props.managerName}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        whitespace: true,*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter the new managers Name'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*  </FormItem>*/}

              {/*  <FormItem>*/}
              {/*    label="Project Status" name="projectStatus" initialValue=*/}
              {/*    {props.projectStatus}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter new status'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*    <Select>*/}
              {/*      <Option key={'true'} value={'true'}>*/}
              {/*        {'true'}*/}
              {/*      </Option>*/}
              {/*      <Option key={'false'} value={'false'}>*/}
              {/*        {'false'}*/}
              {/*      </Option>*/}
              {/*    </Select>*/}
              {/*  </FormItem>*/}
              {/*  <FormItem>*/}
              {/*    label="Project Size" name="projectSize" initialValue=*/}
              {/*    {props.projectSize}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        whitespace: true,*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter the new project Size'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*  </FormItem>*/}
              {/*  <FormItem>*/}
              {/*    label="Quick Win" name="quickWin" initialValue={props.quickWin}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        whitespace: true,*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter if project is Quick win or not'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*  </FormItem>*/}
              {/*  <FormItem>*/}
              {/*    label="Project Type" name="projectType" initialValue=*/}
              {/*    {props.projectType}*/}
              {/*    rules=*/}
              {/*    {[*/}
              {/*      {*/}
              {/*        whitespace: true,*/}
              {/*        required: true,*/}
              {/*        message: 'Please enter the new project type'*/}
              {/*      }*/}
              {/*    ]}*/}
              {/*    >*/}
              {/*  </FormItem>*/}
              {/*</Form>*/}
            </Modal>
            <Button type="primary" onClick={showModal3}>
              Edit
            </Button>
            &nbsp;&nbsp;&nbsp;{' '}
            <Modal
              title="Delete project "
              visible={isModalVisible}
              onOk={handleOk}
              onCancel={handleCancel}
            >
              <p>Are you sure you want to delete this project ?</p>
            </Modal>
            <Button type="primary" onClick={showModal} danger>
              Delete
            </Button>
            <Modal
              title="Archive"
              visible={isModalVisible2}
              onOk={handleOk2}
              onCancel={handleCancel2}
            >
              <p>Are you sure you want to archive this project ?</p>
            </Modal>
            &nbsp;&nbsp;&nbsp;{' '}
            <Button type="primary" onClick={showModal2}>
              Archive
            </Button>
          </>
        )}
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    role: state.auth.role,
    userId: state.auth.userId
  };
};
export default connect(mapStateToProps)(ProjectInfo);
